<template>
    <div>
        <a-tabs class="my-tabs" :animated="false" :default-active-key="key" @change="callback">
            <a-tab-pane key="1" tab="网页客服">
                <Web />
            </a-tab-pane>
            <a-tab-pane key="2" tab="APP">
                <SetApp />
            </a-tab-pane>
            <a-tab-pane key="3" tab="微信公众号">
                <bindWx :type="1" />
            </a-tab-pane>
            <a-tab-pane key="4" tab="微信小程序">
                <bindWx :type="2" />
            </a-tab-pane>
        </a-tabs>
    </div>
</template>
<script>
import Web from "./web";
import BindWx from "./bindWeixin";
import SetApp from "./SetAPP";
export default {
    components: {
        Web,
        BindWx,
        SetApp,
    },
    data() {
        return {
            key: "1",
        };
    },
    created() {
        this.key = this.$route.query.key || "1";
    },
    methods: {
        callback(key) {
            this.key = key;
            this.$router.replace({
                query: {
                    key,
                },
            });
        },
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
.my-tabs > .ant-tabs-bar {
    margin: 0;
}
</style>



