<template>
    <div id="plgin-qfy">
        <a-layout>
            <a-layout-sider style="background-color:#fff;" v-if="list.length">
                <a-tabs
                    class="webmneu"
                    :activeKey="key+'-'+orgId"
                    tab-position="left"
                    @change="callback"
                >
                    <a-tab-pane v-for="i in list" :key="i.id+'-'+i.orgId" :tab="i.name"></a-tab-pane>
                </a-tabs>
            </a-layout-sider>
            <a-layout-content class="main-content" style="background-color:#fff;">
                <div v-show="status==='start'" class="insert_none">
                    <p>您还未添加网页插件</p>
                    <a-button type="primary" size="large" @click="status='url'">添加网页插件</a-button>
                </div>
                <div v-show="status==='url'" class="insert_start">
                    <a-form
                        :form="form"
                        :label-col="{ span: 5 }"
                        :wrapper-col="{ span: 12 }"
                        @submit="handleSubmit"
                    >
                        <a-form-item label="网页名称">
                            <a-input
                                size="large"
                                v-decorator="['name', { validateFirst:true,rules: [{ required: true,message:'请输入网页名称'}]}]"
                            />
                        </a-form-item>
                        <a-form-item
                            :wrapper-col="{ span: 12, offset: 5 }"
                            style="margin-top:60px;"
                        >
                            <a-button
                                size="large"
                                type="primary"
                                html-type="submit"
                                style="width:128px;"
                            >下一步</a-button>
                        </a-form-item>
                    </a-form>
                </div>
                <div v-show="status==='page'" class="insert_two">
                    <div style="display: flex;">
                        <div style="flex: 0 0 50%;">
                            <div class="border">
                                <div
                                    class="checkbox-item"
                                    v-for="(tag,idx) in zdyxButtons"
                                    :key="tag.name"
                                >
                                    <a-checkbox
                                        @change="onChange"
                                        @click="currentIdx=idx"
                                        :checked="tag.checked==='1'"
                                    >{{tag.name}}</a-checkbox>
                                    <p>
                                        <a-icon type="arrow-up" v-if="idx!==0" @click="up(idx)" />
                                        <a-icon
                                            type="arrow-down"
                                            v-if="idx!==zdyxButtons.length-1"
                                            @click="down(idx)"
                                        />
                                    </p>
                                </div>
                            </div>
                            <!-- <div class="border" style="margin-top:30px;"> -->
                            <div style="margin: 20px 0;">
                                <span>按钮位置：</span>
                                <a-select
                                    size="large"
                                    placeholder="请选择按钮位置"
                                    v-model="position"
                                    style="width:100%;margin-top:10px;"
                                    @change="handleSelectChange"
                                >
                                    <a-select-option
                                        v-for="item in positionList"
                                        :key="item.value"
                                        :value="item.value"
                                    >{{item.title}}</a-select-option>
                                </a-select>
                            </div>
                            <!-- </div> -->
                            <div style="margin-top:40px;">
                                <a-button
                                    size="large"
                                    style="width:128px;margin-right:20px;"
                                    @click="status='url'"
                                >上一步</a-button>
                                <a-button
                                    @click="pageNext"
                                    type="primary"
                                    size="large"
                                    style="width:128px;"
                                >下一步</a-button>
                            </div>
                        </div>
                        <div style="flex: 0 0 50%;">
                            <div class="border" style="padding:0;">
                                <div class="radio-item-list">
                                    <a-radio-group v-model="mode" size="large">
                                        <a-radio-button value="pc" style="width:100px;">PC</a-radio-button>
                                        <a-radio-button value="mobile" style="width:100px;">手机</a-radio-button>
                                    </a-radio-group>
                                </div>
                                <div class="myiframe-box" v-if="mode==='pc'">
                                    <img src="../assets/imgs/pc.jpg" />
                                    <div
                                        class="qfy-btn-group"
                                        :style="positionList[position].styleObject"
                                    >
                                        <div
                                            v-for="tag in zdyxButtons"
                                            :key="tag.name"
                                            v-if="tag.checked==='1'"
                                        >{{tag.name}}</div>
                                    </div>
                                </div>
                                <div
                                    class="myiframe-box myiframe-box-mobile"
                                    v-else-if="mode==='mobile'"
                                >
                                    <img src="../assets/imgs/mo.jpg" />
                                    <div
                                        class="qfy-btn-group"
                                        :style="positionList[position].styleObject"
                                    >
                                        <div
                                            v-for="tag in zdyxButtons"
                                            :key="tag.name"
                                            v-if="tag.checked==='1'"
                                        >{{tag.name}}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-show="status==='phone'" class="insert_two">
                    <div style="display: flex;">
                        <div style="flex: 0 0 50%;">
                            <div class="border">
                                <a-form
                                    style="margin-top:20px;"
                                    :form="form2"
                                    :label-col="{ span: 8 }"
                                    :wrapper-col="{ span: 16 }"
                                    @submit="handleSubmit2"
                                >
                                    <a-form-item label="电话号码">
                                        <a-input
                                            size="large"
                                            v-decorator="['phone', { initialValue:phone,rules: [{ required: true,message:'请输入电话号码'}]}]"
                                        />
                                    </a-form-item>
                                    <a-form-item label="电话按钮文字">
                                        <a-input
                                            size="large"
                                            v-decorator="['phonetxt', { initialValue:phonetxt,rules: [{ required: true,message:'请输入电话按钮文字'}]}]"
                                        />
                                    </a-form-item>
                                    <a-form-item
                                        :wrapper-col="{ span: 16, offset: 8 }"
                                        style="margin-top:60px;"
                                    >
                                        <a-button
                                            size="large"
                                            style="width:128px;margin-bottom:20px;margin-right:20px;"
                                            @click="prephone"
                                        >上一步</a-button>
                                        <a-button
                                            size="large"
                                            type="primary"
                                            html-type="submit"
                                            style="margin-left:20px;width:128px;"
                                        >下一步</a-button>
                                    </a-form-item>
                                </a-form>
                            </div>
                        </div>
                        <div style="flex: 0 0 50%;">
                            <div class="border" style="padding:0;">
                                <div class="radio-item-list">
                                    <a-radio-group v-model="mode" size="large">
                                        <a-radio-button value="pc" style="width:100px;">PC</a-radio-button>
                                        <a-radio-button value="mobile" style="width:100px;">手机</a-radio-button>
                                    </a-radio-group>
                                </div>
                                <div class="myiframe-box" v-if="mode==='pc'">
                                    <img src="../assets/imgs/pc.jpg" />
                                    <div
                                        class="qfy-btn-group"
                                        :style="positionList[position].styleObject"
                                    >
                                        <div
                                            v-for="tag in zdyxButtons"
                                            :key="tag.name"
                                            v-if="tag.checked==='1'"
                                        >{{tag.name}}</div>
                                    </div>
                                </div>
                                <div
                                    class="myiframe-box myiframe-box-mobile"
                                    v-else-if="mode==='mobile'"
                                >
                                    <img src="../assets/imgs/mo.jpg" />
                                    <div
                                        class="qfy-btn-group"
                                        :style="positionList[position].styleObject"
                                    >
                                        <div
                                            v-for="tag in zdyxButtons"
                                            :key="tag.name"
                                            v-if="tag.checked==='1'"
                                        >{{tag.name}}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-show="status==='wechat'" class="insert_two">
                    <div style="display: flex;">
                        <div style="flex: 0 0 50%;">
                            <div class="border">
                                <a-form
                                    style="margin-top:20px;"
                                    :form="form3"
                                    :label-col="{ span: 8 }"
                                    :wrapper-col="{ span: 16 }"
                                    @submit="handleSubmit3"
                                >
                                    <a-form-item label="上传二维码">
                                        <a-upload
                                            v-decorator="['wechat', { initialValue:wechat,rules: [{ required: true,message:'请上传二维码'}]}]"
                                            action="/fileupload/upload"
                                            list-type="picture-card"
                                            :show-upload-list="false"
                                            :beforeUpload="beforeUpload"
                                            @change="handleChange"
                                        >
                                            <img
                                                v-if="wechat"
                                                :src="wechat"
                                                style="width:100px;height:100px;"
                                            />
                                            <div v-else>
                                                <a-icon :type="imgLoading?'loading':'plus'" />
                                                <div class="ant-upload-text">上传</div>
                                            </div>
                                        </a-upload>
                                    </a-form-item>
                                    <a-form-item label="微信按钮文字">
                                        <a-input
                                            size="large"
                                            v-decorator="['wechattxt', { initialValue:wechattxt,rules: [{ required: true,message:'请输入微信按钮文字'}]}]"
                                        />
                                    </a-form-item>
                                    <a-form-item
                                        :wrapper-col="{ span: 16, offset: 8 }"
                                        style="margin-top:60px;"
                                    >
                                        <a-button
                                            size="large"
                                            style="width:128px;margin-bottom:20px;margin-right:20px;"
                                            @click="prewechat"
                                        >上一步</a-button>
                                        <a-button
                                            size="large"
                                            style="width:128px;"
                                            type="primary"
                                            html-type="submit"
                                        >下一步</a-button>
                                    </a-form-item>
                                </a-form>
                            </div>
                        </div>
                        <div style="flex: 0 0 50%;">
                            <div class="border" style="padding:0;">
                                <div class="radio-item-list">
                                    <a-radio-group v-model="mode" size="large">
                                        <a-radio-button value="pc" style="width:100px;">PC</a-radio-button>
                                        <a-radio-button value="mobile" style="width:100px;">手机</a-radio-button>
                                    </a-radio-group>
                                </div>
                                <div class="myiframe-box" v-if="mode==='pc'">
                                    <img src="../assets/imgs/pc.jpg" />
                                    <div
                                        class="qfy-btn-group"
                                        :style="positionList[position].styleObject"
                                    >
                                        <div
                                            v-for="tag in zdyxButtons"
                                            :key="tag.name"
                                            v-if="tag.checked==='1'"
                                        >{{tag.name}}</div>
                                    </div>
                                </div>
                                <div
                                    class="myiframe-box myiframe-box-mobile"
                                    v-else-if="mode==='mobile'"
                                >
                                    <img src="../assets/imgs/mo.jpg" />
                                    <div
                                        class="qfy-btn-group"
                                        :style="positionList[position].styleObject"
                                    >
                                        <div
                                            v-for="tag in zdyxButtons"
                                            :key="tag.name"
                                            v-if="tag.checked==='1'"
                                        >{{tag.name}}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-show="status==='code'">
                    <div style="text-align:right;padding:20px;">
                        <a-button size="large" type="primary" @click="addItem">添加网页插件</a-button>
                    </div>
                    <a-form :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
                        <a-form-item label="网页名称">{{name}}</a-form-item>
                        <a-form-item label="安装代码">
                            <div class="code-copy">
                                <pre class="code-content-pre">{{code}}</pre>
                                <p class="code-content-p">{{html}}</p>
                                <pre class="code-content-pre">{{code2}}</pre>
                            </div>
                        </a-form-item>
                        <a-form-item label="操作">
                            <a-button type="danger" @click="deleteItem">删除</a-button>
                            <a-button
                                type="primary"
                                style="margin-left:20px;"
                                @click="status='url'"
                            >编辑</a-button>
                        </a-form-item>
                    </a-form>
                </div>
            </a-layout-content>
        </a-layout>
    </div>
</template>
<script>
export default {
    data() {
        return {
            mode: "pc",
            code: "",
            code2: "",
            type: "add",
            status: "start", // start url page
            form: this.$form.createForm(this, {
                name: "url",
            }),
            name: "",
            zdyxButtons: [
                {
                    checked: "1",
                    type: "1",
                    name: "咨询",
                },
                {
                    checked: "1",
                    type: "2",
                    name: "电话",
                },
                {
                    checked: "1",
                    type: "3",
                    name: "微信",
                },
            ],
            currentIdx: "",
            positionList: [
                {
                    title: "页面左下",
                    value: "0",
                    styleObject: {
                        right: "5px",
                        top: "70%",
                    },
                    styletxt: "right:5px;top:70%;",
                },
                {
                    title: "页面左中",
                    value: "1",
                    styleObject: {
                        right: "5px",
                        top: "50%",
                    },
                    styletxt: "right:5px;top:50%;",
                },
                {
                    title: "页面左上",
                    value: "2",
                    styleObject: {
                        right: "5px",
                        top: "20%",
                    },
                    styletxt: "right:5px;top:20%;",
                },
                {
                    title: "页面右下",
                    value: "3",
                    styleObject: {
                        left: "5px",
                        top: "70%",
                    },
                    styletxt: "left:5px;top:70%;",
                },
                {
                    title: "页面右中",
                    value: "4",
                    styleObject: {
                        left: "5px",
                        top: "50%",
                    },
                    styletxt: "left:5px;top:50%;",
                },
                {
                    title: "页面右上",
                    value: "5",
                    styleObject: {
                        left: "5px",
                        top: "20%",
                    },
                    styletxt: "left:5px;top:20%;",
                },
            ],
            position: "0",
            form2: this.$form.createForm(this, {
                name: "phone",
            }),
            phone: "",
            phonetxt: "电话",
            form3: this.$form.createForm(this, {
                name: "wechat",
            }),
            wechat: "",
            wechattxt: "微信",
            imgLoading: false,
            html: "",
            list: [],
            key: "",
            orgId: "",
            buttons: [],
        };
    },
    created() {
        this.getList();
        this.code2 = ");\n<\/script>";
        // (function (d, s, y, m) {
        //     var w = d.createElement(s);
        //     var h = d.getElementsByTagName("html")[0];
        //     w.setAttribute("id", "qfy-iframe");
        //     w.setAttribute("class", "qfy-iframe");
        //     w.setAttribute("frameborder", "0");
        //     w.setAttribute("src", y);
        //     h.appendChild(w);
        //     var n = d.createElement("script");
        //     n.setAttribute("src", m);
        //     h.appendChild(n);
        // })(
        //     document,
        //     "iframe",
        //     "http://localhost:8080/static/channelSettings/index.html#/web?webid=85342a54f7f24c9eb025f9e4de20f3e3&orgId=880b29c1dae1463ab411e42bac8cdaad",
        //     "http://localhost:8080/static/channelSettings/static/axios.min.js?id=85342a54f7f24c9eb025f9e4de20f3e3&orgId=880b29c1dae1463ab411e42bac8cdaad"
        // );
        this.code =
            "<script>\n(function (d, s, y,m) {\n" +
            "var w = d.createElement(s);\n" +
            "var h = d.getElementsByTagName('html')[0];\n" +
            "w.setAttribute('id', 'qfy-iframe');\n" +
            "w.setAttribute('class', 'qfy-iframe');\n" +
            "w.setAttribute('frameborder', '0');\n" +
            "w.setAttribute('src', y);\n" +
            "h.appendChild(w);\n" +
            "var n = d.createElement('script');\n" +
            "n.setAttribute('src', m);\n" +
            "n.setAttribute('id', 'qfy-pos');\n" +
            "h.appendChild(n);\n" +
            "})(\n" +
            "document,\n'iframe',\n";
    },
    methods: {
        getList() {
            this.$axios.get("/session/channel/site/listJson").then((res) => {
                this.list = res.list;
            });
        },
        callback(keyorgId) {
            if (keyorgId) {
                keyorgId = keyorgId.split("-");
                let key = keyorgId[0];
                this.orgId = keyorgId[1];
                this.$axios
                    .get(
                        `/session/channel/site/infoJson?id=${key}&orgId=${this.orgId}`
                    )
                    .then((res) => {
                        let reqdata = res.data;
                        this.name = reqdata.name;
                        this.form.setFieldsValue({
                            name: reqdata.name,
                        });
                        this.buttons = reqdata.buttons.split(",");
                        let zdyxButtons = [];
                        for (let i = 0; i < this.buttons.length; i++) {
                            let val = this.buttons[i].split("-");
                            let obj = {};
                            if (val[0] === "1") {
                                obj.name = "咨询";
                            } else if (val[0] === "2") {
                                obj.name = "电话";
                            } else if (val[0] === "3") {
                                obj.name = "微信";
                            }
                            obj.checked = val[1];
                            obj.type = val[0];
                            zdyxButtons.push(obj);
                        }
                        this.zdyxButtons = zdyxButtons;
                        this.buttonStyle = reqdata.buttonStyle;
                        this.position = "" + reqdata.buttonPos;
                        this.phone = reqdata.phoneNum;
                        this.phonetxt = reqdata.phoneButtonText;
                        this.form2.setFieldsValue({
                            phone: reqdata.phoneNum,
                            phonetxt: reqdata.phoneButtonText,
                        });
                        this.wechat = reqdata.wxQrUrl;
                        this.wechattxt = reqdata.wxButtonText;
                        this.form3.setFieldsValue({
                            wechat: reqdata.wxQrUrl,
                            wechattxt: reqdata.wxButtonText,
                        });
                        this.status = "code";
                        let nowurl = `'${window.location.origin}/static/channelSettings/index.html#/web?webid=${key}&orgId=${this.orgId}','${window.location.origin}/static/channelSettings/static/axios.min.js?id=${key}&orgId=${this.orgId}'`;
                        this.html = nowurl;
                        this.key = key;
                    });
            }
        },
        addItem() {
            this.status = "url";
            this.name = "";
            this.position = "0";
            this.phone = "";
            this.phonetxt = "电话";
            this.wechat = "";
            this.wechattxt = "微信";
            this.html = "";
            this.key = "";
            this.zdyxButtons = [
                {
                    checked: "1",
                    type: "1",
                    name: "咨询",
                },
                {
                    checked: "1",
                    type: "2",
                    name: "电话",
                },
                {
                    checked: "1",
                    type: "3",
                    name: "微信",
                },
            ];
            this.form.setFieldsValue({
                name: "",
            });
            this.form2.setFieldsValue({
                phone: "",
                phonetxt: "电话",
            });
            this.form3.setFieldsValue({
                wechat: "",
                wechattxt: "微信",
            });
        },
        deleteItem() {
            this.$axios
                .post(`/session/channel/site/delJson`, { id: this.key })
                .then((res) => {
                    this.$message.success("删除成功");
                    this.key = "";
                    this.getList();
                    this.addItem();
                });
        },
        handleSubmit(e) {
            e.preventDefault();
            this.form.validateFields((err, values) => {
                if (!err) {
                    this.status = "page";
                    this.name = values.name;
                }
            });
        },
        onChange(e, f) {
            this.zdyxButtons[this.currentIdx].checked = e.target.checked
                ? "1"
                : "0";
        },
        up(idx) {
            let tags = this.zdyxButtons;
            let arr = tags.splice(idx, 1);
            tags.splice(idx - 1, 0, arr[0]);
            this.tags = tags;
        },
        down(idx) {
            let tags = this.zdyxButtons;
            let arr = tags.splice(idx, 1);
            tags.splice(idx + 1, 0, arr[0]);
            this.tags = tags;
        },
        handleSelectChange(value) {
            let num = parseInt(value);
            this.styleObject = this.positionList[num].styleObject;
        },
        pageNext() {
            for (let i = 0; i < this.zdyxButtons.length; i++) {
                let val = this.zdyxButtons[i];
                if (val.checked === "1") {
                    if (val.type === "2") {
                        this.status = "phone";
                        return;
                    } else if (val.type === "3") {
                        this.status = "wechat";
                        return;
                    }
                }
            }
            for (let i = 0; i < this.zdyxButtons.length; i++) {
                let val = this.zdyxButtons[i];
                if (val.checked === "1" && val.type === "1") {
                    this.status = "code";
                    this.getNowUrl();
                }
            }
        },
        handleSubmit2(e) {
            e.preventDefault();
            this.form2.validateFields((err, values) => {
                if (!err) {
                    this.phone = values.phone;
                    this.phonetxt = values.phonetxt;
                    let num1 = -1;
                    let num2 = -1;
                    for (let i = 0; i < this.zdyxButtons.length; i++) {
                        let val = this.zdyxButtons[i];
                        if (val.checked === "1" && val.type === "2") {
                            num1 = i;
                        }
                        if (val.checked && val.type === "3") {
                            num2 = i;
                        }
                    }
                    if (num2 === -1 || (num2 !== -1 && num1 > num2)) {
                        this.status = "code";
                        this.getNowUrl();
                    } else {
                        this.status = "wechat";
                    }
                }
            });
        },
        handleSubmit3(e) {
            e.preventDefault();
            this.form3.validateFields((err, values) => {
                if (!err) {
                    this.wechat = values.wechat;
                    this.wechattxt = values.wechattxt;
                    let num1 = -1;
                    let num2 = -1;
                    for (let i = 0; i < this.zdyxButtons.length; i++) {
                        let val = this.zdyxButtons[i];
                        if (val.checked && val.type === "3") {
                            num1 = i;
                        }
                        if (val.checked && val.type === "2") {
                            num2 = i;
                        }
                    }
                    if (num2 === -1 || (num2 !== -1 && num1 > num2)) {
                        this.status = "code";
                        this.getNowUrl();
                    } else {
                        this.status = "phone";
                    }
                }
            });
        },
        beforeUpload(file) {
            if (this.imgLoading) {
                return false;
            }
            this.imgLoading = true;
            let arr = [
                "image/jpeg",
                "image/jpg",
                "image/gif",
                "image/png",
                "image/bmp",
            ];
            if (arr.indexOf(file.type) === -1) {
                this.$message.error("照片支持.jpg .jpeg .bmp .gif .png格式");
                return false;
            }
            if (file.size / 1024 / 1024 > 5) {
                this.$message.error("图片大小不超过5M");
                return false;
            }
            return true;
        },
        handleChange({ file, fileList }) {
            if (file.status === "uploading") {
            } else if (file.status === "error") {
                this.imgLoading = false;
            } else if (file.status === "done") {
                this.imgLoading = false;
                let timer = setTimeout(() => {
                    this.form3.setFieldsValue({
                        wechat: file.response.url,
                    });
                    this.wechat = file.response.url;
                    timer && clearTimeout(timer);
                }, 100);
            } else {
                let timer = setTimeout(() => {
                    this.form3.setFieldsValue({
                        wechat: "",
                    });
                    this.wechat = "";
                    timer && clearTimeout(timer);
                }, 100);
            }
        },
        prephone() {
            let num1 = -1;
            let num2 = -1;
            for (let i = 0; i < this.zdyxButtons.length; i++) {
                let val = this.zdyxButtons[i];
                if (val.checked && val.type === "2") {
                    num1 = i;
                }
                if (val.checked && val.type === "3") {
                    num2 = i;
                }
            }
            if ((num2 !== -1 && num1 < num2) || num2 === -1) {
                this.status = "page";
            } else {
                this.status = "wechat";
            }
        },
        prewechat() {
            let num1 = -1;
            let num2 = -1;
            for (let i = 0; i < this.zdyxButtons.length; i++) {
                let val = this.zdyxButtons[i];
                if (val.checked && val.code === "3") {
                    num1 = i;
                }
                if (val.checked && val.code === "2") {
                    num2 = i;
                }
            }
            if ((num2 !== -1 && num1 < num2) || num2 === -1) {
                this.status = "page";
            } else {
                this.status = "phone";
            }
        },
        getNowUrl() {
            let params = {
                name: this.name,
                buttonStyle: "",
                buttonPos: this.position,
                phoneNum: this.phone,
                phoneButtonText: this.phonetxt,
                wxQrUrl: this.wechat,
                wxButtonText: this.wechattxt,
                zdyxButtons: this.zdyxButtons,
            };
            let buttons = "";
            for (let i = 0; i < this.zdyxButtons.length; i++) {
                let b = this.zdyxButtons[i];
                buttons += b.type + "-" + b.checked + (i == 2 ? "" : ",");
            }
            params.buttons = buttons;
            this.key && (params.id = this.key);
            this.$axios
                .post("/session/channel/site/saveJson", params)
                .then((res) => {
                    let nowurl = `'${
                        window.location.origin
                    }/static/channelSettings/index.html#/web?webid=${
                        this.key ? this.key : res.id
                    }&orgId=${res.orgId}','${
                        window.location.origin
                    }/static/channelSettings/static/axios.min.js?id=${
                        this.key ? this.key : res.id
                    }&orgId=${res.orgId}'`;
                    this.html = nowurl;
                    this.orgId = res.orgId;
                    this.key = res.id;
                    this.getList();
                });
        },
        validateToUrl(rule, value, callback) {
            let reg = /^(https?|http):\/\/([a-zA-Z0-9.-]+(:[a-zA-Z0-9.&%$-]+)*@)*((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9][0-9]?)(\.(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9]?[0-9])){3}|([a-zA-Z0-9-]+\.)*[a-zA-Z0-9-]+\.(com|edu|gov|int|mil|net|org|biz|arpa|info|name|pro|aero|coop|museum|[a-zA-Z]{2}))(:[0-9]+)*(\/($|[a-zA-Z0-9.,?'\\+&%$#=~_-]+))*$/;
            if (!reg.test(value)) {
                callback("请输入正确的网址");
            } else {
                callback();
            }
        },
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
#plgin-qfy {
    .code-content-p{
        margin: 0;
        word-break: break-all;
    }
    .myiframe-box {
        height: 600px;
        overflow: hidden;
        position: relative;
        img {
            width: 100%;
            border: 1px solid #c4c7ce;
        }
        .qfy-btn-group {
            position: absolute;
            width: 50px;
            height: 150px;
            margin-top: -75px;
            & > div {
                text-align: center;
                height: 50px;
                line-height: 50px;
                border: 1px solid #c4c7ce;
                cursor: pointer;
                background: #fff;
            }
        }
    }
    .myiframe-box-mobile {
        width: 412px;
        margin: 0 auto;
    }
    .webmneu {
        width: 180px;
        min-height: calc(100vh - 45px);
        margin-left: 20px;
        margin-top: 20px;
        border-left: 1px solid #c4c7ce;
        border-right: 1px solid #c4c7ce;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        overflow: hidden;
        .ant-tabs-left-bar {
            div.ant-tabs-tab {
                width: 180px;
                text-align: center;
                margin: 0;
                padding: 12px 24px;
                border-bottom: 1px solid #c4c7ce;
            }
            div.ant-tabs-tab:first-child {
                border-top: 1px solid #c4c7ce;
            }
            .ant-tabs-tab-active {
                background-color: #1890ff;
                color: #fff;
            }
        }
    }
    .main-content {
        min-height: calc(100vh - 45px);
    }
}
</style>
<style scoped lang="less">
#plgin-qfy {
    pre {
        margin: 0;
    }
    .insert_none {
        margin-top: 14em;
        text-align: center;
        p {
            font-size: 24px;
            line-height: 32px;
            color: #0d1a26;
            margin-bottom: 1em;
            font-weight: 500;
        }
    }

    .code-copy {
        padding: 8px;
        border: 1px solid #c4c7ce;
        line-height: 26px;
    }

    .btn-group-noe {
        margin-top: 2em;
        display: flex;
        justify-content: space-around;
        button:active,
        button:hover,
        button:focus {
            color: rgba(0, 0, 0, 0.65);
            border-color: #d9d9d9;
        }
    }

    .insert_start {
        margin-top: 8em;
    }

    .activeBtn {
        color: #1890ff;
        border-color: #1890ff;
    }

    .insert_two {
        margin: 1em;
    }

    .border {
        border: 1px solid #ebedf0;
        padding: 10px 20px;
        margin: 0 5px;
        box-sizing: border-box;
    }

    .checkbox-item {
        margin: 20px 0;
        display: flex;
        p {
            flex: 1;
            text-align: right;
            i {
                margin-left: 5px;
                cursor: pointer;
            }
        }
    }

    .radio-item-list {
        margin: 20px 0;
        text-align: center;
    }

    .myiframe {
        width: 200%;
        height: 1000px;
        transform: translate(-25%, -25%) scale(0.5);
    }
}
</style>



