<template>
    <div
        v-show="!loading"
        class="qfy-btn-group"
        :style="buttonPos==='1'||buttonPos==='2'||buttonPos==='0'?'right:0':'left:0'"
    >
        <template v-for="zd in zdyxButtons">
            <!-- v-for="(zd,ind) in zdyxButtons" :key="ind" -->
            <div v-if="zd.type==='1'" class="zx-box" id="myBtn" @click="concat">咨询</div>
            <div v-else-if="zd.type==='2'" class="tel-box">
                <span class="tel-txt">{{phoneButtonText}}</span>
                <div
                    class="tel"
                    :style="'left:'+(buttonPos==='1'||buttonPos==='2'||buttonPos==='0'?-152:48)+'px;'"
                >
                    <a @click="call" id="tel" :href="'tel:'+phoneNum">{{phoneNum}}</a>
                </div>
            </div>
            <div v-else-if="zd.type==='3'" class="wechat-box">
                <span class="wechat-txt">{{wxButtonText}}</span>
                <div
                    :class="'wechat '+(c===0?'wechat2':'')"
                    :style="'left:'+(buttonPos==='1'||buttonPos==='2'||buttonPos==='0'?-103:48)+'px;'"
                >
                    <img id="wechat" :src="wxQrUrl" />
                </div>
            </div>
        </template>
    </div>
</template>
<script>
import jsonp from "jsonp";
export default {
    data() {
        return {
            webid: "",
            orgId: "",
            channelId: "",
            positionList: [
                {
                    title: "页面左下",
                    value: "0",
                    styleObject: {
                        right: "5px",
                        top: "70%",
                    },
                    styletxt: "right:5px;top:70%;",
                },
                {
                    title: "页面左中",
                    value: "1",
                    styleObject: {
                        right: "5px",
                        top: "50%",
                    },
                    styletxt: "right:5px;top:50%;",
                },
                {
                    title: "页面左上",
                    value: "2",
                    styleObject: {
                        right: "5px",
                        top: "20%",
                    },
                    styletxt: "right:5px;top:20%;",
                },
                {
                    title: "页面右下",
                    value: "3",
                    styleObject: {
                        left: "5px",
                        top: "70%",
                    },
                    styletxt: "left:5px;top:70%;",
                },
                {
                    title: "页面右中",
                    value: "4",
                    styleObject: {
                        left: "5px",
                        top: "50%",
                    },
                    styletxt: "left:5px;top:50%;",
                },
                {
                    title: "页面右上",
                    value: "5",
                    styleObject: {
                        left: "5px",
                        top: "20%",
                    },
                    styletxt: "left:5px;top:20%;",
                },
            ],
            name: "this.name",
            buttonStyle: "",
            buttonPos: "0",
            phoneNum: "",
            phoneButtonText: "电话",
            wxQrUrl: "",
            wxButtonText: "微信",
            zdyxButtons: [
                {
                    checked: "1",
                    type: "3",
                    name: "微信",
                },
                {
                    checked: "1",
                    type: "1",
                    name: "咨询",
                },
                {
                    checked: "1",
                    type: "2",
                    name: "电话",
                },
            ],
            buttons: [],
            c: -1,
            loading: true,
        };
    },
    created() {
        this.webid = this.$route.query.webid;
        this.orgId = this.$route.query.orgId;
        this.$axios
            .get(
                `/session/channel/site/infoJson?id=${this.webid}&orgId=${this.orgId}`
            )
            .then((res) => {
                let reqdata = res.data;
                this.name = reqdata.name;
                this.buttonStyle = reqdata.buttonStyle;
                this.buttonPos = "" + reqdata.buttonPos;
                this.phoneNum = reqdata.phoneNum;
                this.channelId = reqdata.channelId;
                this.phoneButtonText = reqdata.phoneButtonText;
                this.wxQrUrl = reqdata.wxQrUrl;
                this.wxButtonText = reqdata.wxButtonText;
                this.buttons = reqdata.buttons.split(",");
                let zdyxButtons = [];
                for (let i = 0; i < this.buttons.length; i++) {
                    let val = this.buttons[i].split("-");
                    let obj = {};
                    if (val[0] === "1") {
                        obj.name = "咨询";
                    } else if (val[0] === "2") {
                        obj.name = "电话";
                    } else if (val[0] === "3") {
                        obj.name = "微信";
                    }
                    obj.checked = val[1];
                    obj.type = val[0];
                    zdyxButtons.push(obj);
                }
                this.zdyxButtons = zdyxButtons;
                for (var i = 0; i < this.zdyxButtons.length; i++) {
                    let val = this.zdyxButtons[i];
                    if (val.type === "3" && val.checked === "1") {
                        this.c = i;
                        break;
                    }
                }
                this.loading = false;
            });
    },
    methods: {
        isTerminal() {
            var reg = /(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i;
            if (navigator.userAgent.match(reg)) {
                return "mobile";
            } else {
                return "pc";
            }
        },
        concat() {
            window.open(
                "http://hfw.test.com/guestMessage/index#/conversation?channelId=" +
                    this.channelId +
                    "&orgId=" +
                    this.orgId +
                    "&url=" +
                    location.host,
                "_blank"
            );
        },
        call() {
            if (this.isTerminal() === "pc") {
                jsonp(
                    "http://127.0.0.1:3366/callout?phone=" +
                        this.phoneNum +
                        "&callback=cb_callout",
                    null,
                    () => {}
                );
            }
        },
    },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
body {
    background-color: transparent !important;
}
</style>
<style scoped lang="less">
img {
    border: none;
}
.qfy-btn-group {
    position: absolute;
    height: 156px;
    top: 0;
}
.qfy-btn-group > div {
    width: 50px;
    text-align: center;
    height: 50px;
    line-height: 50px;
    border: 1px solid #c4c7ce;
    border-top: none;
    cursor: pointer;
    position: relative;
    color: #333;
    box-sizing: border-box;
    background-color: #fff;
}
.qfy-btn-group > div:first-child {
    border-top: 1px solid #c4c7ce;
}
.tel-box:hover > .tel {
    display: block;background-color: #fff;
}
.tel {
    position: absolute;
    top: -1px;
    height: 51px;
    width: 150px;
    border: 1px solid #c4c7ce;
    display: none;
    box-sizing: border-box;
}
.tel > a {
    text-decoration: none;
    color: #333;
}
.wechat-box:hover > .wechat {
    display: block;
}
.wechat {
    position: absolute;
    bottom: -1px;
    height: 101px;
    width: 101px;
    border: 1px solid #c4c7ce;
    display: none;
    overflow: hidden;
    box-sizing: border-box;
}
.wechat.wechat2 {
    bottom: -51px;
}
.wechat > img {
    width: 100%;
    height: 100%;
}
</style>



