import Vue from "vue";
import Router from "vue-router";
import Index from "@/pages/index";
import Web from "@/pages/web_vm";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      redirect: "/index"
    },
    {
      path: "/index",
      component: Index
    },
    {
      path: "/web",
      component: Web
    }
  ]
});



