<template>
    <div class="">
      <div class="bindWeixin" v-if="wxApplicationList.length==0">
        <p v-if="type==1" style="font-size:14px;">绑定后你可以使用企蜂客服统一接收与回复顾客咨询</p>
        <p v-if="type==2">将企蜂客服绑定您的微信小程序，请点击下方按钮，按照步骤进行操作</p>
        <div class="bwText">
           <p>{{type==1?'您还未关联微信公众号':'您还未关联微信小程序'}}</p>
           <a-button type="primary" @click="addWeixinApplication" size="large">{{type==1?'添加微信公众号':'添加微信小程序'}}</a-button>
           <span v-if="type==1">你的公众号必须是认证过的微信订阅号或服务号，否则无法正常回复顾客对话</span>
        </div>
      </div>
       <!-- 添加应用程序弹窗 -->
      <a-modal
        :title="type==1?'添加微信公众号':'添加微信小程序'"
        :visible="addWxModal"
        @ok="handleAddOk"
        @cancel="handleAddCancel"
        okText="登录授权"
        cancelText="取消">
        <div class="addAppltion" v-if="type==1">
          <p>建立链接需要您提供以下权限：</p>
          <div>公共账号向用户回复消息，创建菜单权限<br />
              公共账号进行用户管理的权限<br />
              公共账号进行账号的管理权限<br />
              公共账号通知用户权限<br />
              帮助公众号进行网页业务开发<br />
              帮助公众号管理图文消息、图片等素材
          </div>
        </div>
        <div class="addAppltion"  v-if="type==2">
          <p>建立链接需要您提供以下权限：</p>
          <div>帮助小程序获取二维码，进行账号管理<br />
              帮助小程序进行功能开发与数据分析<br />
              帮助小程序接收和发送客服消息<br />
              帮助小程序绑定开放平台账号，实现用户身份打通<br />
              帮助小程序设置名称、头像、简介、类目等基本信息<br />
          </div>
        </div>
      </a-modal>
       <!-- 后台返回的微信授权页面 -->

      <div v-html="html">
         <!-- {{html}} -->
      </div>

      <div v-if="wxApplicationList.length>0 "  class="wxApplication">
        <div class="wxAddBtn" ><a-button type="primary" @click="addWeixinApplication">{{type==1?'添加微信公众号':'添加微信小程序'}}</a-button></div>
        <div class="bindWxList">
          <a-menu mode="vertical" class="wxMenu"  v-model="activeKey" @click='changeMenu'>
            <a-menu-item  v-for="(item,index)  in wxApplicationList"  :key="index">
              {{item.nickName}}
            </a-menu-item>
          </a-menu>
          <div class="wxMenuCon" >
            {{type==1?'公众号名称':'小程序名称'}}：{{wxApplicationList[currentKey].nickName}}<br />
            创建时间：{{wxApplicationList[currentKey].inputTime}}<br />
            操作： <a-button type="primary" @click="relieveBind">立即解除</a-button>
          </div>
        </div>
      </div>
     <!-- 解除绑定弹窗 -->
      <a-modal
        title="解除绑定"
       :visible="relieveBindModal"
        okText="登录微信公众号后台"
        cancelText="取消"
        @ok="handleEelieveOk"
        @cancel="handleRelieveCancel"
       >
        <p>{{type==1?'要解除微信公众号的绑定， 需登录微信公众号后台取消绑定。':'要解除微信小程序的绑定， 需登录微信小程序后台取消绑定。'}}</p>
       </a-modal>
    </div>
</template>
<script>
export default {
    name: "bindWeixin",
    components: {},
    props:['type'],
    data() {
      return {
        addWxModal:false,
        relieveBindModal:false,
        html:"",
        activeKey:[0],
        currentKey:0,
        wxApplicationList:[]
      }
    },
    created(){
      this.getList()
    },
    mounted(){},
    methods: {
      getList(){
        this.$axios.get("/session/channel/wx/listJson",{type:this.type}).then((res) => {
          console.log(res,'小程序列表',this.type);
          this.wxApplicationList = res.list
        })
      },
      addWeixinApplication(){
        this.addWxModal = true
      },
      handleAddOk(){  // 登录授权
        let type = this.type
        this.$axios.get("/open/bind",{type:this.type}).then((res) => {
          console.log(res.url,'授权页面',this.type);
          let nowurl = res.url
          document.location = nowurl
          // if(type==1){
          //   this.html = `<iframe class="qfy-iframe" width="100%" height="500px" frameborder="0" src="${nowurl}"></iframe>`;
          // }else if(type==2){
            // this.html = `<iframe class="qfy-iframe" width="100%" height="500px" frameborder="0" src="${nowurl}"></iframe>`;
          // }
          this.addWxModal = false
        })
      },
      handleAddCancel(){
        this.addWxModal = false
      },
     // 解除绑定
      relieveBind(){
        this.relieveBindModal = true
      },
      handleEelieveOk(){

      },
      handleRelieveCancel(){
        this.relieveBindModal = false
      },
      changeMenu(item){
        this.currentKey = item.key
      }
    }
}
</script>

<style lang="less" scoped>
.bindWeixin{
  padding:0 10px;
  .bwText{
    margin-top: 14em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center ;
    p{
      font-size:24px;
      color:#0d1a26;
      line-height:32px;
    }
    span{
      margin-top:2em;
    }
  }
}
.addAppltion{
  p{
    font-size:14px;
    color:#0d1a26;
  }
  div{
    line-height:25px;
  }
}
.bindWxList{
  width:100%;
  display:flex;

}
.wxApplication{
  margin:0 30px;
  .wxAddBtn{
    text-align:right;margin-bottom:10px;
  }
  .wxMenu{
    width: 256px;margin-right:10px;border:1px solid #ddd;
  }
  .wxMenuCon{
    padding:20px;border:1px solid #ddd;flex:1;line-height:30px;
  }
}
</style>



