<template>
  <div class="setApp">
    <h5>1. 接入SDK下载</h5>
    <div class="selectChannal">
      <a-radio-group  v-model="appChannal">
        <a-radio-button value="ios">
          <img src="../assets/ios.png" alt="" style="margin-right:20px;" width="41px" height="48px"> IOS SDK下载
        </a-radio-button>
        <a-radio-button value="android">
          <img src="../assets/android.png" alt=""  style="margin-right:20px;" width="41px" height="48px">Android SDK下载
        </a-radio-button>
      </a-radio-group>
    </div>
    <div class="channalLink">
     <a href="">IOS SDK接入说明<a-icon type="right" /></a>
     <a href="">Android SDK下载 <a-icon type="right" /></a>
    </div>

    <h5>2. App Key:</h5>
    <div  class="keyInput"><a-input placeholder="请输入" v-model="appKey" /></div>

    <a-button type="primary" @click="addApp">添加一个app</a-button>
    <div class="appTable">
      <a-table bordered :data-source="dataSource" :columns="columns" :rowKey="record=> record.id ">
        <template slot="appName" slot-scope="text, record">
           <img v-if="record.appType == 0" src="../assets/ios.png" width='16px' height="20" alt="">
           <img v-if="record.appType == 1" src="../assets/android.png" width='16px' height="20" alt="">
          {{record.appName}}
        </template>
        <template slot="operation" slot-scope="text, record">
          <a-icon type="edit"  class="icon" @click="handleEditApp(record)" /> <a-icon type="delete" class="icon" @click="handleDeleteApp(record.id)"/>
        </template>
     </a-table>
    </div>
    <!-- 编辑弹窗 -->
    <a-modal v-model="editModal" :title="type=='add'?'添加一app':'编辑app'" @ok="handleEditOkApp" cancelText="取消" okText="确定">
      <a-form-model
       :model="editForm"
       ref="ruleForm"
       :rules="rules"
       :label-col="labelCol"
       :wrapper-col="wrapperCol"
       >
        <a-form-model-item label="请选择平台">
          <a-radio-group v-model="editForm.appType">
            <a-radio :value="0"> IOS </a-radio>
            <a-radio :value="1">Android</a-radio>
          </a-radio-group>
        </a-form-model-item>
         <a-form-model-item label="App名称" prop="appName">
            <a-input v-model="editForm.appName" aria-placeholder="请输入APP名称"/>
         </a-form-model-item>
          <a-form-model-item label="bundle ID" prop="bundleId">
            <a-input v-model="editForm.bundleId" aria-placeholder="请输入 bundle ID"/>
         </a-form-model-item>
    </a-form-model>
    </a-modal>
  </div>
</template>

<script>
export default {
    name: "setApp",
    components: {},
    props:{},
    data() {
        return {
          labelCol: { span: 4 },
          wrapperCol: { span: 14 },
          editForm:{
            id:'',//修改有id
            appType:0,
            appName:'',
            bundleId:''
          },
           rules: {
             appName:[{required: true, message: '请输入名称', trigger: 'blur'}],
             bundleId:[{required: true, message: '请输入ID', trigger: 'blur'}]
             },
          editModal:false,
          appChannal:'ios',
          appKey:'',
          dataSource:[],
          columns: [
            {
              title: '名称',
              dataIndex: 'appName',
              width: '30%',
              scopedSlots: { customRender: 'appName' },
            },
            {
              title: 'bundle ID',
              dataIndex: 'bundleId',
            },
            {
              title: '操作',
              dataIndex: 'operation',
              scopedSlots: { customRender: 'operation' },
            },
          ],
          type:'add'
        }
    },
    created(){
      this.getAppList()
    },
    mounted(){},
    methods: {
      getAppList(){
        let that = this
          this.$axios.get("/session/channel/app/listJson",{}).then((res) => {
            console.log(res.list);
            that.dataSource = res.list
          });
      },
      addApp(){
        this.editModal = true
        this.type="add"
      },
      handleEditApp(row){
        this.type="edit"
        this.editModal = true
        let {id, appType,appName, bundleId} = row
        this.editForm = {
          id,appType,appName,bundleId
        }

      },
      handleDeleteApp(id){
          let that = this
          this.$confirm({
            title: '删除',
            cancelText: '取消',
            okText: '确定',
            content: h => <div style="color:red;">确定要删除吗？</div>,
            onOk() {
              that.$axios.post("/session/channel/app/delJson",{id}).then((res) => {
                that.$message.success('删除成功')
                that.getAppList()
              });
            },
            onCancel() {
              console.log('Cancel');
            },
            class: 'test',
        });

      },
      handleEditOkApp(){
        this.$refs.ruleForm.validate(valid => {
          //添加一个APP
          if(valid){
            let params = {
              appKey:this.appKey,
              ...this.editForm
            }
            console.log(this.editForm)
            this.$axios.post("/session/channel/app/saveJson",params).then((res) => {
              console.log(res,'保存成功');
              this.$message.success('保存成功')
              this.getAppList()
              this.editModal = false
            });
          }
        })
      },
    }
}
</script>

<style lang="less" scoped>
.keyInput{
  width:250px;
  margin:20px 0 40px 0;
}
.setApp{
  margin:0 30px;
  h5{
    font-size:14px;
    color:#333;
    margin-bottom:20px;
    margin-top:30px;
  }
  .appTable{
    margin-top:30px;
  }
  .channalLink{
    display: flex;
    margin-top:15px;
    a{
      width:200px;
      display: block;
      margin-right:20px;
    }
  }
}
.icon{
  font-size:20px;
  margin-right:20px;
}
</style>
<style lang="less">
  .selectChannal .ant-radio-button-wrapper{height:auto;padding:20px 20px; margin-right:30px;}
</style>



